/* blue rgb(47, 56, 143)
 */
body {
  background-color: rgb(47, 56, 143);
}
.container {
  margin:0;
  padding:0;
  width: 100%;
}
/* splash */
.splash {
  height:623px;
  width:100%;
  background:
  linear-gradient(
    rgba(0, 23, 34, 0.75),
    rgba(43, 64, 74, 0.75)
    ),
  url("../../images/splash.jpg");
}
.splash img {
  height: 100px;
  width: auto;
  margin-top: 120px;
  margin-bottom: 35px;
}
.splash h4 {
  font-size: 30px;
  line-height: 30px;
  letter-spacing: 7px;
  color: white;
}
.splash p {
  text-transform: uppercase;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 3px;
  color: #d4d7d8;
}
.title {
  text-transform:uppercase;
  font-weight:700;
  letter-spacing:7;
  font-size: 30px;
  margin: 0 0 20px;
}

.sub-title {
  text-transform:uppercase;
  font-weight:700;
  letter-spacing:7;
  font-size: 18px;
  margin: 0 0 20px;
}

/* navbar */
.navbar {
  height: 58px;
  background-color: white;
  border-radius: 0;
  margin-bottom: 0;
  border: 0;
}
.navbar img {
  height: 40px;
  width: auto;
  margin: 8px 5px 5px 0;
}

.navbar a {
  text-transform:uppercase;
  font-weight: 700;
  padding: 20px 10px;
  color: #263740;
  font-size: 14px;
  line-height: 59px;
  transition: 0.3s;
  text-decoration: none;
}
.navbar a:hover {
  color: rgb(47, 56, 143);
  background-color: rgb(242, 242, 242);
}

/* content */
.content {
  background-color: rgb(47, 56, 143);
  padding: 30px;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAYAAAAGCAYAAADgzO9IAAAAJUlEQVQIW2NkYGCQBGIQeA6lwRQjPglkhXA2SAcygBtLtARcNwBN0gQHoVU8VwAAAABJRU5ErkJggg==);
}
.whiteboxContainer {
  margin: 0 auto 50px;
  text-align: center;
}
.whitebox {
  text-transform: uppercase;
  background-color: white;
  font-weight: 700;
  border-radius: 2;
  font-size: 24px;
  margin: 0 auto;
  padding: 15px 50px;
  color: #263740;
}
